.header-color{
  background: #0F2027;  /* fallback for old browsers */
  background: -webkit-linear-gradient(to left, #2C5364, #203A43, #0F2027);  /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(to left, #2C5364, #203A43, #0F2027); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
}

.landing-grid{
  background: #0F2027;  /* fallback for old browsers */
  background: -webkit-linear-gradient(to left, #2C5364, #203A43, #0F2027);  /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(to left, #2C5364, #203A43, #0F2027); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
  text-align: center;
  position: fixed;
  left: 0;
  right: 0;
  width:100%;
  height:100%;
}

.avatar-img{
  height: 250px;
  padding-top:5em;
}

.banner-text{
  background-color: white;
  opacity: .8;
  width: 75%;
  margin: auto;
  border-radius:10px;
}

.banner-text h1 {
  font-size: 50px;
  font-weight: bold;
  color: black;;
}

.banner-text hr{
  border-text: 5px dotted black;
  width: 50%;
  margin: auto;
}

.banner-text p{
  color: black;
  font-size: 20px;
  padding:1em;
}

.banner-text p2{
  color: black;
  font-size: 18px;
  padding:1em;
}

.social-links {
  display: flex;
  justify-content: space-between;
  width: 300px;
  margin: auto;
}

.social-links i {
  color: white;
  font-size: 8em;
  padding: 8px;
}

.subject-grid {
  display: flex;
}
.subject-grid2 {
  display: flex;
}

.feedback-form {
  display: block;
  margin-bottom:1em;
  display: flex;
  justify-content: center;
  padding-right: 100px;
  padding-left: 100px;
}
.btn-group {
  display: flex;
  justify-content: center;
}
.text-input {
  flex: 1;
  flex-direction: row;
  justify-content: center;
  height: 200px;
  width: 700px;
}
